/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getCharacterConfig } from '../../../configs/characters-config';
import {
  getCharacterBgImageConfig,
  getCharacterDecorImageConfig,
  getCharacterSceneImageConfig,
  getCharacterShineImageConfig
} from '../../../configs/image-configs';
import {
  getArmorEmitterConfig,
  getPersistentSparkleEmitterConfig,
  getSparkleEmitterConfig,
  getWeaponEmitterConfig
} from '../../../configs/particles-configs';
import { getBoostTextConfig } from '../../../configs/text-configs';
import { CharacterState, GameState, GenderType } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { store } from '../../../models/store';
import { makeEmitter, makeImage, makeSpine, makeText, tintSlot, tintSpine } from '../../../utils';
import { sample } from '../../../utils/array/sample';
import { Container } from '../../../utils/container';
import { randomReal } from '../../../utils/number/random-real';

const boostTypes = [
  { name: 'agility', color: '#FFFFFF' },
  { name: 'armor', color: '#5B77DF' },
  { name: 'mana', color: '#5B77DF' },
  { name: 'hp', color: '#E34C4C' },
  { name: 'strong', color: '#FAF694' }
];

export class CharacterView extends Container {
  constructor() {
    super();

    this._config = getCharacterConfig();

    this._build();

    lego.event.on(ModelEvents.CharacterModel.GenderUpdate, this._onCharacterGenderUpdate, this);
    lego.event.on(ModelEvents.CharacterModel.RaceUpdate, this._onCharacterRaceUpdate, this);
    lego.event.on(ModelEvents.CharacterModel.WeaponUpdate, this._onCharacterWeaponUpdate, this);
    lego.event.on(ModelEvents.CharacterModel.ArmorUpdate, this._onCharacterArmorUpdate, this);
    lego.event.on(ModelEvents.CharacterModel.StateUpdate, this._onCharacterStateUpdate, this);
    lego.event.on(ModelEvents.SkillsModel.UpgradesCountUpdate, this._onSkillsModelUpgradesCountUpdate, this);
    lego.event.on(ModelEvents.GameModel.StateUpdate, this._onGameStateUpdate, this);
  }

  get name() {
    return 'CharacterView';
  }

  getBounds() {
    return new Phaser.Rectangle(-225, -500, 450, 600);
  }

  showSparkles() {
    const { x, y } = this._spine;
    makeEmitter(getSparkleEmitterConfig(this, { x, y: y - 200 }));
  }

  showWeaponSparkles() {
    const { x, y } = this._spine;
    const { gender } = store.game.characterModel;

    const config = {
      [GenderType.Male]: {
        pos: { x: x - 100, y: y - 80 },
        start: new Phaser.Point(0, 0),
        end: new Phaser.Point(200, 0)
      },
      [GenderType.Female]: {
        pos: { x: x - 80, y: y - 150 },
        start: new Phaser.Point(0, 0),
        end: new Phaser.Point(200, 40)
      }
    };

    const { start, end, pos } = config[gender];

    makeEmitter(getWeaponEmitterConfig(this, pos, start, end));
  }

  showArmorSparkles() {
    const { x, y } = this._spine;
    makeEmitter(getArmorEmitterConfig(this, { x, y: y - 200 }));
  }

  showPersistentSparkles() {
    const { x, y } = this._spine;
    makeEmitter(getPersistentSparkleEmitterConfig(this, { x, y: y - 200 }));

    this._shines.forEach((shine, index) => {
      shine.visible = true;
      const dir = index ? 1 : -1;
      // @ts-ignore
      this.game.add
        .tween(shine)
        .to({ angle: dir * 360 }, 6000, Phaser.Easing.Linear.None, true, 0, -1, false).universal = true;
    });
  }

  showBoostUp() {
    const { name, color } = sample(boostTypes);
    const text = this._makeBoostText(name, color);
    const { x, y } = text;
    this.game.add.tween(text).to({ x: x + x, y: y + (y + 200) * 2 }, 800, Phaser.Easing.Sinusoidal.Out, true);
    this.game.add
      .tween(text)
      .to({ alpha: 0 }, 500, Phaser.Easing.Sinusoidal.Out, true, 800)
      .onComplete.add(() => {
        text.destroy();
      });
  }

  _makeBoostText(name, color) {
    const text = makeText(getBoostTextConfig(name, color));
    text.position.set(randomReal(-100, 100), randomReal(-100, -300));
    text.anchor.set(0.5);
    this.addChild(text);
    return text;
  }

  _onCharacterGenderUpdate(value) {
    if (value) {
      const { gender, race, weapon, armor } = store.game.characterModel;
      const { spineConfig, skinConfig, tintConfig } = this._config[[gender, race].join('_')];

      this._buildCharacter(spineConfig, skinConfig);
      this._setSpineTint(tintConfig);
    }
  }

  _onCharacterRaceUpdate(value) {
    const { gender, race, weapon, armor } = store.game.characterModel;
    const { spineConfig, skinConfig, tintConfig } = this._config[[gender, race].join('_')];

    this._buildCharacter(spineConfig, skinConfig);
    this._setSpineTint(tintConfig);
  }

  _onCharacterWeaponUpdate(value) {
    const { gender, race, weapon, armor } = store.game.characterModel;
    const { tintConfig } = this._config[[gender, race].join('_')];
    const { skinConfig } = this._config[[gender, race, weapon].join('_')];

    this._spine.setSkin(this._spine.createCombinedSkin('weaponSkin', ...skinConfig));
    this._spine.setToSetupPose();
    this._setSpineTint(tintConfig);
    this.showWeaponSparkles();
  }

  _onCharacterArmorUpdate(value) {
    const { gender, race, weapon, armor } = store.game.characterModel;
    const { skinConfig: prevConfig } = this._config[[gender, race, weapon].join('_')];
    const weaponSkinName = prevConfig[1];
    const { skinConfig, tintConfig } = this._config[[gender, race, armor].join('_')];

    this._spine.setSkin(this._spine.createCombinedSkin('armorSkin', ...skinConfig, weaponSkinName));
    this._spine.setToSetupPose();
    this._setSpineTint(tintConfig);
    this.showArmorSparkles();
  }

  _onCharacterStateUpdate(state) {
    switch (state) {
      case CharacterState.idle:
        this._playIdle();
        break;
      case CharacterState.fun:
        this._playFun();
        break;

      default:
        break;
    }
  }

  _onSkillsModelUpgradesCountUpdate() {
    this.showSparkles();
    this.showBoostUp();
    this.showBgAnimation();
  }

  showBgAnimation() {
    this.game.tweens.remove(this._bgTween);
    this._bg.scale.set(1);
    this._bgTween = this.game.add
      .tween(this._bg.scale)
      .to({ x: 1.4, y: 1.4 }, 200, Phaser.Easing.Cubic.InOut, true, 0, 0, true);
  }

  _onGameStateUpdate(state) {
    switch (state) {
      case GameState.UpgradeComplete:
        this.showBoostUp();
        this.showBoostUp();
        this.showBoostUp();
        this.showBoostUp();
        this.showBoostUp();
        this.showSparkles();
        this.showPersistentSparkles();
        break;

      default:
        break;
    }
  }

  _playIdle() {
    this._spine.addAnimationByName(0, 'idle', true);
  }

  _playFun() {
    this._spine.setAnimationByName(0, 'fun1', false);
    this._spine.addAnimationByName(0, 'idle', true);
  }

  _build() {
    this._buildBg();
    this._buildShine();
    this._buildScene();
    this._buildSilhouette();
    this._buildDecor();
  }

  _buildSilhouette() {
    const unknownCharacters = {
      Male: this._config.unknownMale,
      Female: this._config.unknownFemale,
      None: this._config.unknown
    };
    const { spineConfig, skinConfig } = unknownCharacters[CI_API.Globals.PARAMS.gender_autoselect];
    this._buildCharacter(spineConfig, skinConfig);
    tintSpine(this._spine, 0x000000);
  }

  _buildCharacter(spineConfig, skinConfig, tintConfig = null) {
    if (this._spine) {
      this._spine.destroy();
    }
    this._spine = this._makeCharacter(spineConfig, skinConfig);
    this.addChild(this._spine);
    this._decor && this._decor.bringToTop();
  }

  _setSpineTint(tintConfig) {
    tintConfig.forEach(config => {
      const { slotsNames, color } = config;
      slotsNames.forEach(slotName => {
        tintSlot(this._spine, slotName, color);
      });
    });
  }

  _makeCharacter(spineConfig, skinConfig) {
    const spine = makeSpine(spineConfig.call());
    // spine.setAnimationByName(0, 'idle', true);
    spine.createCombinedSkin('combinedSkin', ...skinConfig);
    spine.setSkinByName('combinedSkin');
    spine.position.set(0, -45);
    return spine;
  }

  _buildBg() {
    const img = makeImage(getCharacterBgImageConfig());
    img.position.set(0, -250);
    this.addChild((this._bg = img));
  }

  _buildShine() {
    this._shines = [];
    for (let i = 0; i < 2; i++) {
      const img = makeImage(getCharacterShineImageConfig());
      img.position.set(0, -200);
      this.addChild(img);
      img.alpha = 0.6;
      img.visible = false;
      this._shines.push(img);
    }
  }

  _buildScene() {
    const img = makeImage(getCharacterSceneImageConfig());
    this.addChild(img);
  }

  _buildDecor() {
    const img = makeImage(getCharacterDecorImageConfig());
    img.position.set(40, -37);
    this.addChild((this._decor = img));
  }
}

