import { lego } from '@armathai/lego';
import { CharacterState } from '../../constants';
import { setFeaturesChoiceCommand } from '../board/set-feature-choice-done-command';
import { setCharacterStateCommand } from '../character/set-character-state-command';
import { setOptionNameCommand } from '../board/set-option-name-command';

export function onOptionClickCommand(uuid) {
  lego.command
    //
    .payload(CharacterState.Unknown)
    .execute(setCharacterStateCommand)

    .payload(uuid)
    .execute(setOptionNameCommand)

    .payload(true)
    .execute(setFeaturesChoiceCommand);
}
