import { GameOverReasons } from '../../constants';
import { delayRunnable, removeRunnable } from '../../utils';
import { ObservableModel } from '../observable-model';

export class CtaModel extends ObservableModel {
  constructor() {
    super('CtaModel');

    this._visible = null;
    this._preVisible = null;
    this._reason = GameOverReasons.Unknown;

    this.makeObservable();
  }

  get reason() {
    return this._reason;
  }

  get preVisible() {
    return this._preVisible;
  }

  get visible() {
    return this._visible;
  }

  destroy() {
    removeRunnable(this._visibilityDelayTimer);
  }

  show(reason, delay = 0) {
    this._preVisible = true;
    this._reason = reason;

    removeRunnable(this._visibilityDelayTimer);
    this._visibilityDelayTimer = delayRunnable(delay, () => {
      this._visible = true;
    });
  }
}
