/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getBoardContentBgImageConfig } from '../../../configs/image-configs';
import { BoardState } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { store } from '../../../models/store';
import { makeImage } from '../../../utils';
import { Container } from '../../../utils/container';
import { FeatureFactory } from './feature-factory';
import { TabBarComponent } from './tab-bar-component';

export class BoardView extends Container {
  constructor() {
    super();

    this._featureFactory = new FeatureFactory();
    this._tabBar = null;
    this._content = null;
    this.alpha = 0;
    this._build();

    lego.event.on(ModelEvents.BoardModel.CurrentFeatureIndexUpdate, this._onCurrentFeatureUpdate, this);
    lego.event.on(ModelEvents.FeatureModel.CurrentOptionNameUpdate, this._onCurrentOptionNameUpdate, this);
    lego.event.on(ModelEvents.FeatureModel.ChoiceDoneUpdate, this._onChoiceDoneUpdate, this);
    lego.event.on(ModelEvents.BoardModel.StateUpdate, this._onBoardStateUpdate, this);
  }

  get name() {
    return 'BoardView';
  }

  get content() {
    return this._content;
  }

  get button() {
    return this._content.button;
  }

  updateBoardPositionInfo() {
    const { x, y } = this;
    this.boardPosition = { x, y };
    this.game.tweens.remove(this._showTween);
    this.position.set(x, y);
  }

  hide(force = false) {
    if (force) {
      this.x = this.boardPosition.x + 900;
    } else {
      this.game.add
        .tween(this)
        .to({ x: this.boardPosition.x + 900 }, 300, Phaser.Easing.Cubic.In, true)
        .onComplete.add(() => {
          lego.event.emit(ViewEvents.BoardView.HideComplete);
        });
    }
  }

  show() {
    this.alpha = 1;
    this._showTween = this.game.add.tween(this).to({ x: this.boardPosition.x }, 300, Phaser.Easing.Cubic.In, true);
    this._showTween.onComplete.add(() => {
      lego.event.emit(ViewEvents.BoardView.ShowComplete);
    });
  }

  destroy() {
    lego.event.off(ModelEvents.BoardModel.CurrentFeatureIndexUpdate, this._onCurrentFeatureUpdate, this);
    lego.event.off(ModelEvents.FeatureModel.CurrentOptionNameUpdate, this._onCurrentOptionNameUpdate, this);
    lego.event.off(ModelEvents.FeatureModel.ChoiceDoneUpdate, this._onChoiceDoneUpdate, this);
    lego.event.off(ModelEvents.BoardModel.StateUpdate, this._onBoardStateUpdate, this);

    this.game.add
      .tween(this)
      .to({ x: this.boardPosition.x + 900 }, 300, Phaser.Easing.Cubic.In, true)
      .onComplete.add(() => {
        super.destroy();
      });
  }

  _build() {
    this.addChild((this._tabBar = new TabBarComponent()));
    this.addChild((this._bg = this._buildBg()));
  }

  _onCurrentFeatureUpdate(featureIndex) {
    this._updateTabs(featureIndex);
    this._updateContent();
  }

  _onCurrentOptionNameUpdate(name) {
    this._content.updateSelectorPosition(name);
  }

  _onChoiceDoneUpdate(value) {
    if (value) {
      this._content.showButton();
    }
  }

  _onBoardStateUpdate(state, prevState, uuid) {
    switch (state) {
      case BoardState.HideStart:
        this.hide();
        break;
      case BoardState.ShowStart:
        this.show();
        break;

      default:
        break;
    }
  }

  _updateContent() {
    const { x, y } = this._bg;
    if (this._content) {
      this._content.destroy();
      this._content = null;
    }

    const { options, name } = store.game.boardModel.currentFeature;

    const content = this._featureFactory.getFeatureView(options, name);
    content.buttonClick.addOnce(this._onButtonClick, this);
    content.optionClick.add(this._onOptionClick, this);

    this.addChild((this._content = content));
    this._content.position.set(x, y);
  }

  _onButtonClick() {
    lego.event.emit(ViewEvents.BoardView.AcceptClick);
  }

  _onOptionClick(uuid) {
    lego.event.emit(ViewEvents.BoardView.OptionClick, uuid);
  }

  _updateTabs(featureIndex) {
    const { currentFeature, features } = store.game.boardModel;
    this._tabBar.setTabActive(currentFeature.name);

    for (let i = 0; i < featureIndex; i++) {
      const { name } = features[i];
      this._tabBar.setTabSelected(name);
    }
  }

  _buildBg() {
    const gap = 10;
    this._tabBar.updateTransform();
    const { x, y, width, height } = this._tabBar.getBounds();

    const img = makeImage(getBoardContentBgImageConfig());
    img.position.set(x + width / 2, y + img.height / 2 + height + gap);

    return img;
  }
}
