import { lego } from '@armathai/lego';
import { hintParamGuard } from '../../../guards/ad/hint-param-guard';
import { initializeBoardModelCommand } from '../../game/initialize-board-model-command';
import { initializeHintModelCommand } from '../hint/initialize-hint-model-command';
import { destroyTutorialModelCommand } from './destroy-tutorial-model-command';

export function tutorialCompleteCommand() {
  lego.command
    .execute(destroyTutorialModelCommand)

    .execute(initializeBoardModelCommand)

    .guard(hintParamGuard)
    .execute(initializeHintModelCommand);
}
