export const Spines = {
  BeastManFemale: {
    Key: 'hero_beastman_female',
    Animations: {
      Fun: 'fun1',
      Idle: 'idle'
    }
  },
  BeastManMale: {
    Key: 'hero_beastman_male',
    Animations: {
      Fun: 'fun1',
      Idle: 'idle'
    }
  },
  HumanFemale: {
    Key: 'hero_human_female',
    Animations: {
      Fun: 'fun1',
      Idle: 'idle'
    }
  },
  HumanMale: {
    Key: 'hero_human_male',
    Animations: {
      Fun: 'fun1',
      Idle: 'idle'
    }
  },
  OrcFemale: {
    Key: 'hero_orc_female',
    Animations: {
      Fun: 'fun1',
      Idle: 'idle'
    }
  },
  OrcMale: {
    Key: 'hero_orc_male',
    Animations: {
      Fun: 'fun1',
      Idle: 'idle'
    }
  }
};

export function getBeastManFemaleSpineConfig(x, y) {
  return {
    key: Spines.BeastManFemale.Key,
    scale: { x: 1.1, y: 1.1 },
    x,
    y
  };
}

export function getBeastManMaleSpineConfig(x, y) {
  return {
    key: Spines.BeastManMale.Key,
    x,
    y
  };
}

