import { lego } from '@armathai/lego';
import { GameState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { getViewByProperty, searchAtlasByFrame } from '../../utils';

export class HintView extends Phaser.Sprite {
  constructor() {
    const texture = searchAtlasByFrame('ui/hand.png');
    super(CI_API.game, 0, 0, texture.key, texture.frame);
    this.anchor.set(0.5, 0.7);

    lego.event.on(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);
    this._hide();
  }

  destroy() {
    lego.event.off(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);

    super.destroy();
  }

  _onHintVisibleUpdate(visible) {
    visible ? this._show() : this._hide();
  }

  _show() {
    switch (store.game.state) {
      case GameState.CreateCharacter:
        this._showHintOnBoard();
        break;
      case GameState.UpgradeCharacter:
        this._showHintOnButton('SkillBoardView');
        break;

      default:
        break;
    }
  }

  _showHintOnBoard() {
    const { choiceDone } = store.game.boardModel.currentFeature;
    choiceDone ? this._showHintOnButton('BoardView') : this._showHintOnOptions();
  }

  _showHintOnOptions() {
    const boardView = getViewByProperty('name', 'BoardView');

    const { unlockedOptions } = store.game.boardModel.currentFeature;
    const options = unlockedOptions.map(unlockedOption => boardView.content.getOptionByUuid(unlockedOption.uuid));

    const localPoses = options.map(option => {
      const { position, parent } = option;
      return this.parent.toLocal(position, parent);
    });

    this._showMultiplePointHintTween(localPoses);
  }

  _showHintOnButton(viewName) {
    const { button } = getViewByProperty('name', viewName);
    const pos = this.parent.toLocal(button.parent.toGlobal(new Phaser.Point(button.x, button.y)), undefined);

    this._showOnePointHint(pos, { x: button.width * 0.5 + this.width * 0.15, y: button.height * 0.2 });
  }

  _showOnePointHint(pos, offset) {
    const { x: offX, y: offY } = offset;
    this._killMoveTweens();
    this.position.set(pos.x + offX, pos.y + offY);
    this.angle = 0;
    this.visible = true;
    this.game.tweens.remove(this._clickTween);
    this._clickTween = this.game.add
      .tween(this)
      .to({ angle: -30 }, 200, Phaser.Easing.Linear.None, true, 300, -1, true);
    this._clickTween.repeatDelay(800, -1);
  }

  _showMultiplePointHintTween(localPoses) {
    const totalDuration = 2000;
    this.visible = true;

    this._killMoveTweens();

    const poses = [...localPoses];

    poses.reverse();
    const initialPos = { x: poses[0].x + 600, y: poses[0].y };
    const { x: initialX, y: initialY } = initialPos;
    this.position.set(initialX, initialY);
    poses.push(initialPos);

    this._tweens = [];
    poses.forEach((pos, index) => {
      const { x, y } = pos;

      const tw = this.game.add
        .tween(this)
        .to({ x: x + 60, y }, totalDuration / poses.length, Phaser.Easing.Sinusoidal.InOut, false, 500);

      const tw2 = this.game.add.tween(this).to({ angle: -30 }, 200, Phaser.Easing.Linear.None, false, 0, 0, true);

      tw.chain(tw2);

      if (index) {
        this._tweens[this._tweens.length - 1].chain(tw);
      }

      this._tweens.push(tw, tw2);
    });

    this._tweens[0].start();
    this._tweens[this._tweens.length - 1].onComplete.add(() => {
      this._showMultiplePointHintTween(localPoses);
    });
  }

  _killMoveTweens() {
    if (this._tweens) {
      this._tweens.forEach(tw => {
        tw.stop();
        this.game.tweens.remove(tw);
      });
      this._tweens = null;
    }

    this.angle = 0;
  }

  _hide() {
    this._killMoveTweens();
    this.game.tweens.removeFrom(this);
    this.visible = false;
  }
}
