const sparkle = 'sparkle';
const persistentSparkle = 'persistentSparkle';
const buttonSparkle = 'buttonSparkle';
const weaponSparkle = 'weaponSparkle';

function getSparkleParticlesConfig() {
  return {
    key: sparkle,
    data: {
      lifespan: 800,
      frame: ['fx/fx_star.png'],
      scale: {
        initial: 0,
        value: 1,
        control: [
          { x: 0, y: 0.5 },
          { x: 0.3, y: 1 },
          { x: 1, y: 0 }
        ]
      },
      alpha: {
        initial: 0,
        value: 1,
        control: [
          { x: 0, y: 0.5 },
          { x: 0.3, y: 1 },
          { x: 1, y: 0 }
        ]
      },
      vy: { min: -6, max: 6 },
      vx: { min: -6, max: 6 },
      rotation: { delta: { min: -10, max: 10 } }
    }
  };
}

