export function getPersistentSparkleEmitterConfig(parent, pos) {
  const { x, y } = pos;
  return {
    key: persistentSparkle,
    parent,
    x,
    y,
    force: { x: 0, y: 0 },
    config: {
      zone: CI_API.game.particleStorm.createCircleZone(50),
      total: 10,
      repeat: -1,
      frequency: 500
    }
  };
}
