import { CellAlign } from '@armathai/phaser2-grid';
import { isSquareLikeScreen } from '../../utils';

export function getGameGridLandscapeConfig() {
  return {
    // debug: { color: 0x2fc900 },
    name: 'game',
    cells: [
      {
        name: 'character',
        bounds: { x: 0, y: 0, width: 0.5, height: 1 },
        align: CellAlign.RightCenter,
        offset: {
          y: -20
        }
      },
      {
        name: 'board',
        bounds: { x: 0.5, y: 0.28, width: 0.48, height: 0.55 },
        align: CellAlign.CenterTop
      }
    ]
  };
}

export function getGameGridPortraitConfig() {
  const boardHeight = isSquareLikeScreen ? 0.26 : 0.28;
  return {
    // debug: { color: 0x2fc900 },
    name: 'game',
    cells: [
      {
        name: 'character',
        bounds: { x: 0, y: 0.12, width: 1, height: 0.51 },
        align: CellAlign.CenterBottom,
        offset: {
          y: 60
        }
      },
      {
        name: 'board',
        bounds: { x: 0, y: 0.63, width: 1, height: boardHeight },
        align: CellAlign.CenterTop
      }
    ]
  };
}
