/* eslint-disable class-methods-use-this */
import { getSkillIconImageConfig } from '../../../configs/image-configs';
import { getSkillProgressBarTextConfig } from '../../../configs/text-configs';
import { makeImage, makeText } from '../../../utils';
import { Container } from '../../../utils/container';
import { ProgressComponent } from './progress-component';

export class SkillProgressBarComponent extends Container {
  constructor(name, title, fill) {
    super();

    this._name = name;
    this._build(name, title, fill);
  }

  get name() {
    return this._name;
  }

  updateProgress(value, force) {
    this._progress.updateProgress(value, force);
  }

  _build(name, title, fill) {
    this._buildIcon(name);
    this._buildProgress(fill);
    this._buildLabel(title);
  }

  _buildIcon(name) {
    const img = makeImage(getSkillIconImageConfig(name));
    img.anchor.set(0, 0.5);
    this.addChild((this._icon = img));
  }

  _buildLabel(title) {
    const { x, y, width } = this._progress;
    const gap = 20;

    const label = makeText(getSkillProgressBarTextConfig(title));
    label.anchor.set(0.5);
    label.position.set(x - width / 2 + label.width / 2 + gap, y + 3);
    this.addChild((this._label = label));
  }

  _buildProgress(fill) {
    const { x, y, width } = this._icon;
    const gap = 20;

    this.addChild((this._progress = new ProgressComponent(fill)));
    this._progress.position.set(x + width + gap + this._progress.width / 2, y);
  }
}
