/* eslint-disable class-methods-use-this */
import { BaseContentComponent } from './base-content-component';

export class TwoContentComponent extends BaseContentComponent {
  buildButton() {
    super.buildButton();
    this.button.position.set(0, 65);
  }

  setOptionsPosition() {
    this.optionsPositions = [
      { x: -180, y: 0 },
      { x: 180, y: 0 }
    ];
    super.setOptionsPosition();
  }

  hideButton() {
    if (CI_API.Globals.PARAMS.gender_autoselect === 'None') {
      this.button.alpha = 0;
      this.button.visible = false;
    }
  }
}
