import { lego } from '@armathai/lego';
import { GameOverReasons, GameState } from '../../../constants';
import { store } from '../../../models/store';
import { setCharacterFinalSkinCommand } from '../../character/set-character-final-skin-command';
import { initializeSkillsModelCommand } from '../../game/initialize-skills-model-command';
import { setGameModelStateCommand } from '../../game/set-game-model-state-command';

export function ctaIdleTimeCommand() {
  lego.command
    .execute(setCharacterFinalSkinCommand)

    .payload(GameState.UpgradeComplete)
    .execute(setGameModelStateCommand)

    .payload(true)
    .execute(initializeSkillsModelCommand);

  store.ad.cta.show(GameOverReasons.Idled, 0);
}
