/* eslint-disable class-methods-use-this */
import { getTutorialButtonConfig } from '../../configs/button-configs';
import { getHandViewImageConfig } from '../../configs/image-configs';
import { getTutorialTextConfig } from '../../configs/text-configs';
import { fitText, makeImage, makeText } from '../../utils';
import { Button } from '../../utils/button/button';
import { Container } from '../../utils/container';

export class TutorialSequenceView extends Container {
  constructor(config) {
    super();
    this._showTween = null;
    this._hideTween = null;

    this.addChild((this._button = this._buildButton()));
    this.addChild((this._label = this._buildLabel(config.text)));
    this.addChild((this._hand = this._buildHand()));

    this.alpha = 0;
  }

  getBounds() {
    const { x: lx, y: ly, width: lw, height: lh } = this._label.getBounds();
    const { x: bx, y: by, width: bw, height: bh } = this._button.getBounds();

    const x = Math.min(bx, lx);
    const y = Math.min(by, ly);
    const w = Math.max(bw, lw);
    const h = Math.max(bh, lh);

    return new Phaser.Rectangle(x, y, w, h);
  }

  rebuild() {
    this._button.destroy();
    this.addChild((this._button = this._buildButton()));
    const { x, width } = this._button;
    this.bringToTop(this._hand);
    this._hand.x = x + width / 2;
  }

  destroy() {
    this.game.tweens.remove(this._showTween);
    this.game.tweens.remove(this._hideTween);
    this._destroyHand();

    super.destroy();
  }

  _destroyHand() {
    if (this._hand) {
      this.game.tweens.remove(this._handShowTween);
      this.game.tweens.remove(this._handHideTween);
      this.game.tweens.remove(this._handClickTween);
      this._hand.destroy();
      this._hand = null;
    }
  }

  show() {
    this._showTween = this.game.add.tween(this).to({ alpha: 1 }, 300, Phaser.Easing.Linear.None, true);
    this._showTween.onComplete.add(() => {
      this._showHand();
    });
    return this._showTween;
  }

  hide() {
    this._hideTween = this.game.add.tween(this).to({ alpha: 0 }, 300, Phaser.Easing.Linear.None, true);
    return this._hideTween;
  }

  _showHand() {
    const { x, y, width } = this._button;
    const handInitialPos = x + 500;
    this._hand.x = handInitialPos;

    this._handShowTween = this.game.add
      .tween(this._hand)
      .to({ alpha: 1, x: x + width / 2, y }, 500, Phaser.Easing.Linear.None, true);

    this._handShowTween.onStart.add(() => {
      this._hand.alpha = 0;
      this._hand.visible = true;
    });

    this._handShowTween.onComplete.add(() => {
      this._handClickTween = this.game.add
        .tween(this._hand)
        .to({ angle: -30 }, 200, Phaser.Easing.Linear.None, true, 0, 0, true);

      // @ts-ignore
      this._handClickTween.universal = true;

      this._handHideTween = this.game.add
        .tween(this._hand)
        .to({ alpha: 0, x: handInitialPos, y }, 500, Phaser.Easing.Linear.None, true, 1000);
      this._handHideTween.onComplete.add(() => {
        this._hand.visible = false;
      });

      // @ts-ignore
      this._handHideTween.universal = true;
    });
  }

  _buildButton() {
    return new Button(getTutorialButtonConfig());
  }

  _buildLabel(text) {
    const { height } = this._button;
    const textWidth = 460;

    const label = makeText(getTutorialTextConfig(text, textWidth * 1.2));
    label.anchor.set(0.5, 1);
    label.position.set(0, -height / 2 - 30);
    fitText(label, textWidth);

    return label;
  }

  _buildHand() {
    const hand = makeImage(getHandViewImageConfig());
    hand.visible = false;
    hand.anchor.set(0.5, 0.7);
    return hand;
  }
}
