import { FONT1 } from '../constants';
import { randomInt } from '../utils/number/random-int';

// TUTORIAL
export function getTutorialTextConfig(text, width) {
  return {
    text: `${text}`,
    style: {
      font: FONT1,
      fontSize: 42,
      fill: '#feeb46',
      align: 'center',
      wordWrap: true,
      wordWrapWidth: width
    }
  };
}

export function getTutorialButtonTextConfig() {
  return {
    text: 'tutorial_btn_text',
    style: {
      font: FONT1,
      fontSize: 50,
      fill: '#ffffff',
      stroke: '#8a240d',
      strokeThickness: 7
    }
  };
}

