export function getContentTextConfig(featureName, width, fontSize = 34, wordWrap = true) {
  return {
    text: {
      text: `Choose Text`,
      macros: { '{{0}}': featureName.toLowerCase() },
      ignoreLocalization: false,
      toString: function toString() {
        return this;
      }
    },
    style: {
      font: FONT1,
      fontSize,
      align: 'center',
      fill: '#ffea46',
      stroke: '#0f6187',
      strokeThickness: 5,
      wordWrap,
      wordWrapWidth: width
    }
  };
}

