export function getBoostTextConfig(name, color) {
  return {
    text: {
      text: `${name}`,
      macros: { '{{0}}': `+${randomInt(1, 5)}` },
      ignoreLocalization: false,
      toString: function toString() {
        return this;
      }
    },
    style: {
      font: FONT1,
      fontSize: 40,
      fill: color
    }
  };
}
