/* eslint-disable no-underscore-dangle */
import { objectKeys } from './object/object-keys';
import { UniversalTween } from './tween/universal-tween';

function updateTextSize(textGameObject, fontSize) {
  const styleRef = textGameObject.style;
  styleRef.fontSize = fontSize;
  textGameObject.setStyle(styleRef);
}

export const searchAtlasByFrame = frame => {
  const atlas = imageLoader._atlases.find(a => {
    const f = CI_API.game.cache.getFrameByName(a, frame);
    return !!f;
  });
  return atlas ? { key: atlas, frame } : { key: frame };
};

export const delayRunnable = (delay, runnable, context, ...args) => {
  return CI_API.game.time.events.add(delay, runnable, context, ...args);
};

export const removeRunnable = runnable => {
  return CI_API.game.time.events.remove(runnable);
};

export const loopRunnable = (delay, runnable, context, ...args) => {
  return CI_API.game.time.events.loop(delay, runnable, context, ...args);
};

export const postRunnable = (runnable, context, ...args) => {
  delayRunnable(CI_API.game.time.physicsElapsedMS, runnable, context, ...args);
};

export function fitText(textGameObject, width, height) {
  if (textGameObject.__defaultFontSize__) {
    updateTextSize(textGameObject, textGameObject.__defaultFontSize__);
  }

  const { fontSize, width: textWidth, height: textHeight } = textGameObject;
  const ratioW = width ? width / textWidth : 1;
  const ratioH = height ? height / textHeight : 1;
  const ratio = Math.min(Math.min(ratioW, ratioH), 1);

  if (typeof fontSize === 'number') {
    const newFontSize = fontSize * ratio;
    updateTextSize(textGameObject, newFontSize);
  }
}

export const getGameBounds = () => {
  const { width, height } = CI_API.game;

  return new Phaser.Rectangle(0, 0, width, height);
};

export function tweenNumber(config) {
  const {
    from,
    to,
    duration,
    onUpdate = () => {},
    onComplete = () => {},
    onStart = () => {},
    context,
    step = 1
  } = config;
  let localStep = 0;
  const tweenObj = { from, to };
  const t = CI_API.game.add.tween(tweenObj).to({ from: to }, duration, Phaser.Easing.Sinusoidal.Out, true);
  t.onStart.addOnce(() => {
    onStart.call(context);
  });
  t.onUpdateCallback(tween => {
    localStep += 1;
    if (localStep === step) {
      onUpdate.call(context, tween.target.from);
      localStep = 0;
    }
  });
  t.onComplete.addOnce(() => {
    tweenObj.from = to;
    onUpdate.call(context, tweenObj.from);
    onComplete.call(context);
  });
  return t;
}

export function getViewByProperty(prop, value, parent) {
  const { children } = parent || CI_API.game.world;

  if (!children || children.length === 0) {
    return null;
  }

  for (let i = 0; i < children.length; i += 1) {
    const child = children[i];
    if (child[prop] === value) {
      return child;
    }

    const view = getViewByProperty(prop, value, child);
    if (view) {
      return view;
    }
  }

  return null;
}

export function makeText(config) {
  const { text, style = {}, x = 0, y = 0 } = config;
  const { shadow, gradient, lineSpacing = 0, fontSize } = style;

  const label = CI_API.game.add.text(x, y, text, style);
  label.lineSpacing = lineSpacing;

  if (fontSize) {
    // @ts-ignore
    label.__defaultFontSize__ = fontSize;
  }

  if (shadow) {
    const { x: shadowX, y: shadowY, color, blur, shadowStroke, shadowFill } = shadow;
    label.setShadow(shadowX, shadowY, color, blur, shadowStroke, shadowFill);
  }

  if (gradient) {
    //  Here we create a linear gradient on the Text context.
    //  This uses the exact same method of creating a gradient as you do on a normal Canvas context.
    const grd = label.context.createLinearGradient(0, 0, 0, label.height);
    gradient.forEach((g, index) => {
      grd.addColorStop(index, g);
    });
    label.fill = grd;
  }

  return label;
}

export function makeSpine(config) {
  const { key, x = 0, y = 0, scalingVariant, scale = { x: 1, y: 1 }, skin } = config;
  const spine = CI_API.game.add.spine(x, y, key, scalingVariant);
  spine.scale.copyFrom(scale);
  skin && spine.setSkinByName(skin);
  return spine;
}

export function makeAnimation(config) {
  const { key, x = 0, y = 0, data } = config;
  const animation = imageLoader.spriteMake(x, y, key);
  data.forEach(d => {
    const { name, prefix, start, stop, suffix, zeroPad, frameRate, loop } = d;
    animation.animations.add(
      name,
      Phaser.Animation.generateFrameNames(prefix, start, stop, suffix, zeroPad),
      frameRate,
      loop
    );
  });
  return animation;
}

export function makeImage(config) {
  const { frame, x = 0, y = 0, tint, scale = { x: 1, y: 1 }, anchor = { x: 0.5, y: 0.5 } } = config;
  const { x: sx, y: sy } = scale;
  const { x: ax, y: ay } = anchor;

  const img = imageLoader.spriteMake(x, y, frame);

  if (tint) img.tint = tint;
  img.scale.set(sx, sy);
  img.anchor.set(ax, ay);

  return img;
}

export function makeNinePatch(config) {
  const { frame, x = 0, y = 0, width, height, tint, anchor = { x: 0.5, y: 0.5 } } = config;
  const { x: ax, y: ay } = anchor;
  const texture = searchAtlasByFrame(frame);
  const ninePatch = CI_API.game.add.ninePatch(x, y, texture.key, texture.frame, width, height);
  if (tint) ninePatch.tint = tint;
  ninePatch.anchor.set(ax, ay);

  return ninePatch;
}

export function makeEmitter(config) {
  const { key, parent, x = 0, y = 0, config: emitterConfig, force = { x: 0, y: 0 } } = config;
  const emitter = CI_API.game.particleStorm.createEmitter();
  emitter.addToWorld(parent);
  emitter.force.set(force.x, force.y);
  return emitter.emit(key, x, y, emitterConfig);
}

export const makePixel = (() => {
  let texture = null;

  return config => {
    if (!texture) {
      texture = CI_API.game.make
        .graphics(0, 0)
        .beginFill(0xffffff)
        .drawRect(0, 0, 4, 4)
        .endFill()
        .generateTexture();
    }
    const { x = 0, y = 0, width, height, alpha = 1, tint = 0x0 } = config;
    const pixel = CI_API.game.make.sprite(x, y, texture);
    pixel.width = width;
    pixel.height = height;
    pixel.tint = tint;
    pixel.alpha = alpha;

    return pixel;
  };
})();

export function updateUniversalTweenData(tw) {
  const { timeline } = tw;
  const vEnd = tw.getPropertiesEndValue();
  const vStart = tw.getPropertiesStartValue();

  timeline.forEach((tl, index) => {
    tw.updateTweenData('vEnd', vEnd, index);
    tw.updateTweenData('vStart', vStart, index);
  });

  tw.resume();
}

export function completeTween(tw) {
  const { timeline, target } = tw;
  tw.stop();
  CI_API.game.tweens.removeFrom(target);

  timeline.forEach(tl => {
    // @ts-ignore
    const { vEnd, vStart, yoyo } = tl;
    const keys = objectKeys(vEnd);

    keys.forEach(k => {
      target[k] = yoyo ? vStart[k] : vEnd[k];
    });
  });

  tw.onComplete.dispatch();
}

export function manageUniversalTweens() {
  const tweens = CI_API.game.tweens.getAll();
  tweens.forEach(tw => {
    if (tw instanceof UniversalTween) {
      updateUniversalTweenData(tw);

      return;
    }

    // @ts-ignore
    if (tw.universal === true) {
      tw.resume();

      return;
    }

    completeTween(tw);
  });
}

export function makeUniversalTween(config) {
  const {
    target,
    startValues,
    endValues,
    context,
    duration = 1000,
    easing = null,
    autoStart = false,
    delay = 0,
    repeat = 0,
    yoyo = false,
    from = false
  } = config;

  const sValues = from ? endValues : startValues;
  const eValues = from ? startValues : endValues;

  const tw = new UniversalTween(target, sValues, eValues, context);
  tw.to(eValues.call(context), duration, easing, autoStart, delay, repeat, yoyo);

  return tw;
}

export function getRelativeScale(source, destination) {
  const { width: sourceWidth, height: sourceHeight } = source.getLocalBounds();
  const { width: destinationWidth, height: destinationHeight } = destination.getBounds();

  return new Phaser.Point(
    destinationWidth / (sourceWidth * source.parent.worldScale.x),
    destinationHeight / (sourceHeight * source.parent.worldScale.y)
  );
}

export function getRelativePosition(source, destination) {
  return source.parent.toLocal(destination.parent.toGlobal(destination.position));
}

export function getRelativeTransform(source, destination) {
  return { scale: getRelativeScale(source, destination), position: getRelativePosition(source, destination) };
}

export function isSquareLikeScreen() {
  const bounds = getGameBounds();
  return Math.min(bounds.width, bounds.height) / Math.max(bounds.width, bounds.height) > 0.7;
}

export function tintSlot(spine, slotName, color) {
  const rgb = Phaser.Color.hexToRGBArray(color);
  const slot = spine.skeleton.findSlot(slotName);
  if (!slot) {
    const slots = spine.skeleton.slots.filter(slot => slot.data && slot.data.attachmentName === slotName);
    slots.forEach(slot => {
      slot.r = rgb[0];
      slot.g = rgb[1];
      slot.b = rgb[2];
    });
  }

  if (slot) {
    slot.r = rgb[0];
    slot.g = rgb[1];
    slot.b = rgb[2];
  }
}

export function tintSpine(spine, tint) {
  const { slots } = spine.skeleton;
  for (let i = 0; i < slots.length; i++) {
    const slot = slots[i];
    if (slot.currentSprite) slot.currentSprite.tint = tint;
    const rgb = Phaser.Color.hexToRGBArray(tint);
    slot.r = rgb[0];
    slot.g = rgb[1];
    slot.b = rgb[2];
  }
}

export function upperCaseFirstLetter(text) {
  return text.charAt(0).toUpperCase() + text.slice(1);
}
