/* eslint-disable class-methods-use-this */
import {
  getLockIconImageConfig,
  getOptionBgImageConfig,
  getOptionIconImageConfig
} from '../../../../configs/image-configs';
import { InputPriority } from '../../../../constants';
import { makeImage } from '../../../../utils';
import { Container } from '../../../../utils/container';

export class BaseOptionComponent extends Container {
  constructor(name, uuid, locked) {
    super();

    this._optionName = name;
    this._uuid = uuid;
    this._locked = locked;
    this.bgScaleValue = 0.66;

    this.optionClick = new Phaser.Signal();

    this.build();
  }

  get uuid() {
    return this._uuid;
  }

  get optionName() {
    return this._optionName;
  }

  get locked() {
    return this._locked;
  }

  build() {
    this.buildBg();
    this.buildIcon();
    this.buildLock();
  }

  buildLock() {
    if (this._locked) {
      const img = makeImage(getLockIconImageConfig());
      img.position.set(-30, 30);
      this.addChild(img);
    }
  }

  buildIcon() {
    const icon = makeImage(getOptionIconImageConfig(this.optionName));
    this.addChild((this.icon = icon));
    if (this._locked) {
      icon.alpha = 0.6;
    }
  }

  buildBg() {
    const bg = makeImage(getOptionBgImageConfig());
    bg.scale.set(this.bgScaleValue);
    bg.alpha = 0;
    this.addChild((this.bg = bg));

    if (!this._locked) {
      this.bg.inputEnabled = true;
      this.bg.name = 'option';
      this.bg.input.priorityID = InputPriority.Game;
      this.bg.events.onInputDown.add(() => {
        this.optionClick.dispatch(this._uuid);
      });
    }
  }
}
