import { ObservableModel } from '../../observable-model';
import { OptionModel } from './option-model';

export class FeatureModel extends ObservableModel {
  constructor(name) {
    super(`FeatureModel`);

    this._name = name;
    this._options = null;
    this._currentOptionName = undefined;
    this._choiceDone = false;

    this.makeObservable('_currentOptionName', '_choiceDone');
  }

  get name() {
    return this._name;
  }

  get options() {
    return this._options;
  }

  get currentOption() {
    return this._options[this._currentOptionName];
  }

  get currentOptionName() {
    return this._currentOptionName;
  }

  set currentOptionName(value) {
    this._currentOptionName = value;
  }

  get unlockedOptions() {
    return this._options.filter(option => !option.locked);
  }

  get choiceDone() {
    return this._choiceDone;
  }

  set choiceDone(value) {
    this._choiceDone = value;
  }

  getOptionByUuid(uuid) {
    return this._options.find(option => option.uuid === uuid);
  }

  getOptionByName(name) {
    return this._options.find(option => option.name === name);
  }

  initialize(options) {
    this._options = options.map(option => {
      const { key, locked } = option;
      return new OptionModel(key, locked);
    });
  }
}
