/* eslint-disable class-methods-use-this */
import { getContentButtonConfig } from '../../../configs/button-configs';
import { getOptionBgImageConfig, getOptionSelectorImageConfig } from '../../../configs/image-configs';
import { getContentTextConfig } from '../../../configs/text-configs';
import { fitText, makeImage, makeText } from '../../../utils';
import { Button } from '../../../utils/button/button';
import { Container } from '../../../utils/container';
import { OptionFactory } from './option-factory';

export class BaseContentComponent extends Container {
  constructor(name, options) {
    super();

    this.buttonClick = new Phaser.Signal();
    this.optionClick = new Phaser.Signal();
    this.optionFactory = new OptionFactory();
    this.optionsPositions = [];

    this.build(name, options);
    this.hideButton();

    this.button.onClick.add(this.onButtonClick, this);
  }

  getOptionByName(name) {
    return this.options.find(option => option.optionName === name);
  }

  getOptionByUuid(uuid) {
    return this.options.find(option => option.uuid === uuid);
  }

  onButtonClick() {
    this.buttonClick.dispatch();
  }

  hideButton() {
    this.button.alpha = 0;
    this.button.visible = false;
  }

  showButton() {
    this.game.add
      .tween(this.button)
      .to({ alpha: 1 }, 300, Phaser.Easing.Cubic.InOut, true, 400)
      .onStart.add(() => {
        this.button.visible = true;
      });
  }

  updateSelectorPosition(name) {
    if (name) {
      const option = this.getOptionByName(name);
      const { x, y } = option;

      this.moveSelector(x, y);
    } else {
      this.selector.alpha = 0;
      this.selector.visible = false;
    }
  }

  moveSelector(x, y) {
    if (this.selector.visible) {
      this.game.tweens.remove(this.selectorTween);
      this.selectorTween = this.game.add.tween(this.selector).to({ x, y }, 350, Phaser.Easing.Linear.None, true);
    } else {
      this.game.add
        .tween(this.selector)
        .to({ alpha: 1 }, 350, Phaser.Easing.Linear.None, true)
        .onStart.add(() => {
          this.selector.position.set(x, y);
          this.selector.visible = true;
        });
    }
  }

  build(name, options) {
    this.buildOptionsBgContainer();
    this.buildSelector();
    this.buildOptions(options);
    this.setOptionsPosition();
    this.buildButton();
    this.buildLabel(name);
  }

  buildButton() {
    const btn = new Button(getContentButtonConfig());
    this.addChild((this.button = btn));
  }

  buildLabel(name) {
    const label = makeText(getContentTextConfig(name, 200));
    label.lineSpacing = -20;
    label.anchor.set(0.5);
    label.position.set(0, -25);
    fitText(label, 220, 250);
    this.addChild((this.label = label));
  }

  buildOptions(options) {
    this.options = options.map(optionModel => {
      const { uuid, name, locked } = optionModel;
      const option = this.optionFactory.getOptionView(name, uuid, locked);
      option.optionClick.add(uuid => {
        this.optionClick.dispatch(uuid);
      });
      this.addChild(option);

      const bg = makeImage(getOptionBgImageConfig());
      bg.scale.set(option.bgScaleValue);
      this.bgContainer.addChild(bg);
      return option;
    });
  }

  buildSelector() {
    const img = makeImage(getOptionSelectorImageConfig());
    this.addChild((this.selector = img));
    this.selector.scale.set(0.9);
    this.selector.alpha = 0;
    this.selector.visible = false;
  }

  buildOptionsBgContainer() {
    const container = new Container();
    this.addChild((this.bgContainer = container));
  }

  setOptionsPosition() {
    const bgs = this.bgContainer.children;
    this.options.forEach((option, i) => {
      const { x, y } = this.optionsPositions[i];
      option.position.set(x, y);
      bgs[i].position.set(x, y);
    });
  }
}
