import { SkillsModelState } from '../../../constants';
import { ObservableModel } from '../../observable-model';

export const upgradesCount = 9;
const initialValues = [0.5, 0.7, 0.4];

export class SkillsModel extends ObservableModel {
  constructor() {
    super('SkillsModel');

    this._health = null;
    this._power = null;
    this._armor = null;
    this._level = null;
    this._state = SkillsModelState.Unknown;
    this._upgradesComplete = false;
    this._upgradesCount = 0;

    this.makeObservable('_state', '_level', '_armor', '_power', '_health', '_upgradesComplete', '_upgradesCount');
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  get health() {
    return this._health;
  }

  set health(value) {
    this._health = value;
  }

  get power() {
    return this._power;
  }

  set power(value) {
    this._power = value;
  }

  get armor() {
    return this._armor;
  }

  set armor(value) {
    this._armor = value;
  }

  get level() {
    return this._level;
  }

  set level(value) {
    this._level = value;
  }

  get upgradesCount() {
    return this._upgradesCount;
  }

  upgradeSkills() {
    const values = [this._health, this._power, this._armor].map((value, i) => {
      return value + (1 - initialValues[i]) / upgradesCount;
    });

    this._health = values[0];
    this._power = values[1];
    this._armor = values[2];

    this._upgradesCount++;
  }

  initialize(filled) {
    this._health = filled ? 1 : initialValues[0];
    this._power = filled ? 1 : initialValues[1];
    this._armor = filled ? 1 : initialValues[2];
  }
}
