/* eslint-disable class-methods-use-this */
import { BaseOptionComponent } from './base-option-component';

export class FemaleOptionComponent extends BaseOptionComponent {
  buildIcon() {
    super.buildIcon();
    this.icon.scale.set(-1, 1);
  }

  buildBg() {
    this.bgScaleValue = 0.9;
    super.buildBg();
  }
}
