/* eslint-disable class-methods-use-this */

import { ClassType, GenderType, RaceType, WeaponType } from '../../../constants';
import { BoneOptionComponent } from './options/bone-option-component';
import { BookOptionComponent } from './options/book-option-component';
import { CatOptionComponent } from './options/cat-option-component';
import { CurveSwordOptionComponent } from './options/curve-sword-option-component';
import { DragonOptionComponent } from './options/dragon-option-component';
import { DwarfOptionComponent } from './options/dwarf-option-component';
import { ElfOptionComponent } from './options/elf-option-component';
import { FemaleOptionComponent } from './options/female-option-component';
import { FireSwordOptionComponent } from './options/fire-sword-option-component';
import { FishSwordOptionComponent } from './options/fish-sword-option-component';
import { GoldenOptionComponent } from './options/golden-option-component';
import { HammerOptionComponent } from './options/hammer-option-component';
import { HumanOptionComponent } from './options/human-option-component';
import { MaleOptionComponent } from './options/male-option-component';
import { NormalSwordOptionComponent } from './options/normal-sword-option-component';
import { OrcOptionComponent } from './options/orc-option-component';
import { RoyalOptionComponent } from './options/royal-option-component';

export class OptionFactory {
  getOptionView(name, uuid, locked) {
    let view;
    switch (name) {
      case GenderType.Male:
        view = new MaleOptionComponent(name, uuid, locked);
        break;
      case GenderType.Female:
        view = new FemaleOptionComponent(name, uuid, locked);
        break;
      case RaceType.Human:
        view = new HumanOptionComponent(name, uuid, locked);
        break;
      case RaceType.Orc:
        view = new OrcOptionComponent(name, uuid, locked);
        break;
      case RaceType.Cat:
        view = new CatOptionComponent(name, uuid, locked);
        break;
      case RaceType.Dwarf:
        view = new DwarfOptionComponent(name, uuid, locked);
        break;
      case RaceType.Elf:
        view = new ElfOptionComponent(name, uuid, locked);
        break;
      case WeaponType.NormalSword:
        view = new NormalSwordOptionComponent(name, uuid, locked);
        break;
      case WeaponType.FishSword:
        view = new FishSwordOptionComponent(name, uuid, locked);
        break;
      case WeaponType.CurveSword:
        view = new CurveSwordOptionComponent(name, uuid, locked);
        break;
      case WeaponType.FireSword:
        view = new FireSwordOptionComponent(name, uuid, locked);
        break;
      case WeaponType.Hammer:
        view = new HammerOptionComponent(name, uuid, locked);
        break;
      case ClassType.Bone:
        view = new BoneOptionComponent(name, uuid, locked);
        break;
      case ClassType.Dragon:
        view = new DragonOptionComponent(name, uuid, locked);
        break;
      case ClassType.Golden:
        view = new GoldenOptionComponent(name, uuid, locked);
        break;
      case ClassType.Book:
        view = new BookOptionComponent(name, uuid, locked);
        break;
      case ClassType.Royal:
        view = new RoyalOptionComponent(name, uuid, locked);
        break;

      default:
        break;
    }

    return view;
  }
}
