import { CharacterState, RaceType } from '../../constants';
import { ObservableModel } from '../observable-model';

export const upgradesCount = 9;
const initialValues = [0.5, 0.7, 0.4];

export class CharacterModel extends ObservableModel {
  constructor() {
    super('CharacterModel');

    this._state = CharacterState.Unknown;
    this._gender = null;
    this._race = null;
    this._weapon = null;
    this._armor = null;

    this.makeObservable('_gender', '_race', '_weapon', '_armor', '_state');
    this.initialize();
  }

  get gender() {
    return this._gender;
  }

  set gender(value) {
    this._gender = value;
  }

  get race() {
    return this._race;
  }

  set race(value) {
    this._race = value;
  }

  get weapon() {
    return this._weapon;
  }

  set weapon(value) {
    this._weapon = value;
  }

  get armor() {
    return this._armor;
  }

  set armor(value) {
    this._armor = value;
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  initialize() {
    this._race = RaceType.Human;
  }
}
