import { BoardState } from '../../../constants';
import { ObservableModel } from '../../observable-model';
import { FeatureModel } from './feature-model';

export class BoardModel extends ObservableModel {
  constructor(config) {
    super('BoardModel');
    this._config = config;
    this._state = BoardState.Unknown;
    this._features = null;
    this._currentFeatureIndex = null;

    this.makeObservable('_currentFeatureIndex', '_state');
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  get features() {
    return this._features;
  }

  get currentFeature() {
    return this._features[this._currentFeatureIndex];
  }

  get currentFeatureIndex() {
    return this._currentFeatureIndex;
  }

  getFeatureByUuid(uuid) {
    return this._features.find(feature => feature.uuid === uuid);
  }

  getFeatureConfig(name) {
    return this._config.features.find(featureConfig => featureConfig.name === name);
  }

  setCurrentFeatureIndex(index) {
    this._currentFeatureIndex = index;
    const featureConfig = this.getFeatureConfig(this.currentFeature.name);
    const { current } = featureConfig;
    this.currentFeature.currentOptionName = current;
  }

  initialize() {
    const { features } = this._config;

    this._features = features.map(featureConfig => {
      const { name, options } = featureConfig;

      const featureModel = new FeatureModel(name);
      featureModel.initialize(options);

      return featureModel;
    });
  }
}
