import { lego } from '@armathai/lego';
import { CharacterState } from '../../constants';
import { store } from '../../models/store';
import { setCharacterPropertyCommand } from '../character/set-character-property-command';
import { setCharacterStateCommand } from '../character/set-character-state-command';

export function onFeatureModelCurrentOptionNameUpdateCommand(value, prevValue, uuid) {
  const feature = store.game.boardModel.getFeatureByUuid(uuid);

  lego.command
    //
    .payload(feature.name, value)
    .execute(setCharacterPropertyCommand)

    .guard(() => value)
    .payload(CharacterState.idle)
    .execute(setCharacterStateCommand);
}
