/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getGameGridConfig } from '../../configs/grid-configs';
import { GameState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { BoardView } from './board/board-view';
import { CharacterView } from './character/character-view';
import { SkillBoardView } from './skill_board/skill-board-view';

export class GameView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    super.build(this.getGridConfig());

    this._boardView = null;

    lego.event.on(ModelEvents.GameModel.StateUpdate, this._onGameStateUpdate, this);
    lego.event.on(ModelEvents.GameModel.CharacterModelUpdate, this._onCharacterModelUpdate, this);
  }

  get name() {
    return 'GameView';
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);

    this._boardView && this._boardView.updateBoardPositionInfo();
    this._skillBoard && this._skillBoard.updateBoardPositionInfo();
  }

  getGridConfig() {
    return getGameGridConfig();
  }

  _onGameStateUpdate(state) {
    switch (state) {
      case GameState.CreateCharacter:
        this._buildBoard();
        break;
      case GameState.UpgradeCharacter:
        this._boardView && this._destroyBoard();
        this._buildSkillBoard();
        break;

      default:
        break;
    }
  }

  _onCharacterModelUpdate(value) {
    if (value) {
      this._buildCharacter();
    }
  }

  _buildBoard() {
    this.setChild('board', (this._boardView = new BoardView()));
    this._boardView.updateBoardPositionInfo();
    this._boardView.hide(true);
    this._boardView.show();
  }

  _buildCharacter() {
    this.setChild('character', (this._characterView = new CharacterView()));
  }

  _destroyBoard() {
    this._boardView.destroy();
    this._boardView = null;
  }

  _buildSkillBoard() {
    if (!this._skillBoard) {
      this.setChild('board', (this._skillBoard = new SkillBoardView()));
      this._skillBoard.updateBoardPositionInfo();
      this._skillBoard.show();
    }
  }

  _destroySkillBoard() {
    this._skillBoard.destroy();
    this._skillBoard = null;
  }
}
