import { fitText, makeEmitter, makeImage, makeNinePatch, makeText } from '..';
import { getButtonEmitterConfig } from '../../configs/particles-configs';
import { AbstractButton } from './abstract-button';

export class Button extends AbstractButton {
  createState(config) {
    const state = new Phaser.Group(this.game);

    const { bg, label } = config;

    // FRAME
    const bgObj = bg.width && bg.height ? makeNinePatch(bg) : makeImage(bg);
    state.add(bgObj);

    // LABEL
    if (label) {
      const labelObj = makeText(label);
      labelObj.anchor.set(0.5);
      state.add(labelObj);

      fitText(labelObj, bgObj.width * 0.85, bgObj.height * 0.9);
    }

    return this.add(state);
  }
}

export class FancyButton extends Button {
  constructor(config) {
    super(config);
  }

  showSparkle() {
    const { width } = this.getBottom();
    makeEmitter(
      getButtonEmitterConfig(this, { x: 0, y: 0 }, new Phaser.Point(-width / 2, 0), new Phaser.Point(width / 2, 0))
    );
  }

  getBounds() {
    // @ts-ignore
    return this.states.down ? this.states.up.getBounds() : super.getBounds();
  }

  onUp(target, pointer, isOver) {
    super.onUp(target, pointer, isOver);
    this.showSparkle();
  }
}
