/* eslint-disable class-methods-use-this */
import { FeaturesTitle, FeaturesType } from '../../../constants';
import { Container } from '../../../utils/container';
import { TabComponent } from './tab-component';

function getTabBarConfig() {
  return {
    tabs: [
      {
        name: FeaturesType.Gender,
        title: FeaturesTitle.Gender,
        bg: { frame: 'tab_left', width: 160, fitWidth: 90 },
        icon: { frame: 'icon_gender', position: { x: -40, y: -4 } },
        separator: null
      },
      {
        name: FeaturesType.Race,
        title: FeaturesTitle.Race,
        bg: { frame: 'tab_middle', width: 160, fitWidth: 80 },
        icon: { frame: 'icon_race', position: { x: -25, y: -3 } },
        separator: { position: { x: -66, y: 0 } }
      },
      {
        name: FeaturesType.Weapon,
        title: FeaturesTitle.Weapon,
        bg: { frame: 'tab_middle', width: 160, fitWidth: 90 },
        icon: { frame: 'icon_weapon', position: { x: -40, y: -4 } },
        separator: { position: { x: -66, y: 0 } }
      },
      {
        name: FeaturesType.Class,
        title: FeaturesTitle.Class,
        bg: { frame: 'tab_right', width: 140, fitWidth: 60 },
        icon: { frame: 'icon_class', position: { x: -25, y: -5 } },
        separator: { position: { x: -66, y: 0 } }
      }
    ]
  };
}

export class TabBarComponent extends Container {
  constructor() {
    super();

    this._build();
  }

  get tabs() {
    return this._tabs;
  }

  getTabByName(name) {
    return this._tabs.find(tab => tab.tabName === name);
  }

  setTabActive(name) {
    this.getTabByName(name).setActive();
  }

  setTabSelected(name) {
    this.getTabByName(name).setSelected();
  }

  _build() {
    this._buildTabBar();
  }

  _buildTabBar() {
    const { tabs } = getTabBarConfig();
    this._tabs = [];

    tabs.forEach((tabConfig, i) => {
      const { name, title, icon, bg, separator } = tabConfig;
      const tab = new TabComponent(name, title, icon, bg, separator);
      this.addChild(tab);
      const x = this._tabs[i - 1] ? this._tabs[i - 1].x + tab.width * 0.86 : 0;

      tab.position.set(x, 0);
      this._tabs.push(tab);
    });
  }
}
