import { lego } from '@armathai/lego';
import { AdStatus } from '../../constants';
import { hintModelGuard } from '../../guards/ad/hint-model-guard';
import { persistentCtaModelGuard } from '../../guards/ad/persistent-cta-model-guard';
import { tutorialModelGuard } from '../../guards/ad/tutorial-model-guard';
import { gameModelGuard } from '../../guards/game/game-model-guard';
import { destroyGameModelCommand } from '../game/destroy-game-model-command';
import { mapPlayableCommandsCommand } from '../game/map-playable-commands-command';
import { unmapPlayableCommandsCommand } from '../game/unmap-playable-commands-command';
import { initializeModelsCommand } from '../initialize-models-command';
import { restartGameStateCommand } from '../states/restart-game-state-command';
import { destroyHintModelCommand } from './hint/destroy-hint-model-command';
import { destroyPersistentCtaModelCommand } from './pcta/destroy-persistent-cta-model-command';
import { destroyTutorialModelCommand } from './tutorial/destroy-tutorial-model-command';

export function adStatusUpdateCommand(status) {
  switch (status) {
    case AdStatus.Game:
      lego.command.execute(mapPlayableCommandsCommand, initializeModelsCommand);
      break;

    case AdStatus.Restart:
      lego.command.execute(restartGameStateCommand);
      break;

    case AdStatus.PreCta:
      lego.command
        .execute(unmapPlayableCommandsCommand)

        .guard(tutorialModelGuard)
        .execute(destroyTutorialModelCommand)

        .guard(hintModelGuard)
        .execute(destroyHintModelCommand);
      break;

    case AdStatus.Cta:
      lego.command
        .guard(gameModelGuard)
        .execute(destroyGameModelCommand)

        .guard(persistentCtaModelGuard)
        .execute(destroyPersistentCtaModelCommand);

      break;

    case AdStatus.Retry:
      //
      break;
    default:
  }
}
