import { lego, not } from '@armathai/lego';
import { BoardState } from '../../constants';
import { lastFeatureGuard } from '../../guards/board/last-feature-guard';
import { setBoardStateCommand } from '../board/set-board-state-command';
import { initializeSkillsModelCommand } from '../game/initialize-skills-model-command';

export function onAcceptButtonClickCommand() {
  lego.command
    //
    .payload(BoardState.HideStart)
    .guard(not(lastFeatureGuard))
    .execute(setBoardStateCommand)

    .guard(lastFeatureGuard)
    .execute(initializeSkillsModelCommand);
}
