import { ClassType, FeaturesType, GenderType, RaceType, WeaponType } from '../constants';

export function getBoardConfig(initialGender) {
  return {
    features: [
      {
        name: FeaturesType.Gender,
        current: initialGender,
        options: [
          { key: GenderType.Male, locked: false },
          { key: GenderType.Female, locked: false }
        ]
      },
      {
        name: FeaturesType.Race,
        current: RaceType.Human,
        options: [
          { key: RaceType.Human, locked: false },
          { key: RaceType.Orc, locked: false },
          { key: RaceType.Cat, locked: false },
          { key: RaceType.Dwarf, locked: true },
          { key: RaceType.Elf, locked: true }
        ]
      },
      {
        name: FeaturesType.Weapon,
        current: null,
        options: [
          { key: WeaponType.NormalSword, locked: false },
          { key: WeaponType.FishSword, locked: false },
          { key: WeaponType.CurveSword, locked: false },
          { key: WeaponType.FireSword, locked: false },
          { key: WeaponType.Hammer, locked: false }
        ]
      },
      {
        name: FeaturesType.Class,
        current: null,
        options: [
          { key: ClassType.Bone, locked: false },
          { key: ClassType.Dragon, locked: false },
          { key: ClassType.Golden, locked: false },
          { key: ClassType.Book, locked: true },
          { key: ClassType.Royal, locked: true }
        ]
      }
    ]
  };
}
