/* eslint-disable class-methods-use-this */
import { getContentButtonConfig } from '../../../configs/button-configs';
import { getContentTextConfig } from '../../../configs/text-configs';
import { fitText, makeText } from '../../../utils';
import { Button } from '../../../utils/button/button';
import { BaseContentComponent } from './base-content-component';

export class FiveContentComponent extends BaseContentComponent {
  buildButton() {
    const btn = new Button(getContentButtonConfig(240, 60, 36));
    this.addChild((this.button = btn));
    btn.position.set(0, 60);
  }

  showButton() {
    super.showButton();
    this.game.add.tween(this.label).to({ alpha: 0 }, 400, Phaser.Easing.Cubic.InOut, true);
  }

  buildLabel(name) {
    const { x, y } = this.button;
    const label = makeText(getContentTextConfig(name, 200, 42, false));
    label.anchor.set(0.5);
    label.position.set(x, y);
    fitText(label, 500, 100);
    this.addChild((this.label = label));
  }

  buildSelector() {
    super.buildSelector();
    this.selector.scale.set(0.65);
  }

  setOptionsPosition() {
    const y = -34;
    this.optionsPositions = [
      { x: -220, y },
      { x: -110, y },
      { x: 0, y },
      { x: 110, y },
      { x: 220, y }
    ];
    super.setOptionsPosition();
  }
}
