/* eslint-disable class-methods-use-this */
import { getLoaderBgPatchConfig, getLoaderFillPatchConfig } from '../../../configs/ninepatch-configs';
import { makeNinePatch } from '../../../utils';
import { Container } from '../../../utils/container';

export class ProgressComponent extends Container {
  constructor(fillFrame) {
    super();

    this._build(fillFrame);
    this._fill.mask = this._fillMaskRect;
  }

  updateProgress(value, force) {
    const { x, y, width, height } = this._bg;
    this._updateGrWidth(this._fxRect, width * value - 2, this._fxRect.height);
    if (!force) {
      this._updateGrWidth(this._fillMaskRect, width * value, height);
    } else {
      this.game.tweens.remove(this.updateTween);
      const obj = { value: this._fillMaskRect.width };
      this.updateTween = this.game.add
        .tween(obj)
        .to({ value: width * value }, 500, Phaser.Easing.Sinusoidal.Out, true)
        .onUpdateCallback(() => {
          this._updateGrWidth(this._fillMaskRect, obj.value, height);
        });
    }
  }

  _updateGrWidth(gr, w, h) {
    const { x, y, width, height } = this._bg;
    gr.clear();
    gr.beginFill(0xffffff);
    gr.drawRect(x - width / 2, y - h / 2, w, h);
    gr.endFill();
  }

  _build(fillFrame) {
    this._buildBg();
    this._buildFxLayer();
    this._buildFill(fillFrame);
    this._buildMask();
  }

  _buildBg() {
    const img = makeNinePatch(getLoaderBgPatchConfig());
    this.addChild((this._bg = img));
  }

  _buildFill(frame) {
    const img = makeNinePatch(getLoaderFillPatchConfig(frame));
    this.addChild((this._fill = img));
  }

  _buildFxLayer() {
    const gr = this.game.add.graphics();
    this.addChild((this._fxRect = gr));
    gr.x += 2;
    const { width, height } = getLoaderFillPatchConfig();
    this._updateGrWidth(gr, width, height);
  }

  _buildMask() {
    const gr = this.game.add.graphics();
    this.addChild((this._fillMaskRect = gr));

    const { width, height } = this._bg;
    this._updateGrWidth(gr, width, height);
  }
}
