import { lego } from '@armathai/lego';
import { store } from '../../../models/store';
import { nextTutorialSequenceCommand } from './next-tutorial-sequence-command';
import { setTutorialCompleteCommand } from './set-tutorial-complete-command';

export function tutorialSequenceViewCompleteCommand() {
  const { sequences } = store.ad.tutorial;

  switch (sequences.length) {
    case 0:
      lego.command.execute(setTutorialCompleteCommand);
      break;
    default:
      lego.command.execute(nextTutorialSequenceCommand);
  }
}
