export function getContentButtonConfig(width = 190, height = 60, fontSize = 26) {
  const config = {
    input: {
      name: 'accept_button',
      priority: InputPriority.Game
    },
    states: {
      up: {
        bg: getContentButtonPatchConfig(width, height),
        label: getContentBtnTextConfig(fontSize)
      }
    }
  };

  if (CI_API.Globals.PARAMS.button_response) {
    config.states.down = {
      bg: getContentButtonDownPatchConfig(width, height),
      label: getContentBtnTextConfig(fontSize, '#a7a7a7')
    };
  }

  return config;
}

export function getUpgradeButtonConfig() {
  const config = {
    input: {
      name: 'upgrade_button',
      priority: InputPriority.Game
    },
    states: {
      up: {
        bg: getUpgradeButtonPatchConfig(),
        label: getUpgradeBtnTextConfig()
      }
    }
  };

  if (CI_API.Globals.PARAMS.button_response) {
    config.states.down = {
      bg: getUpgradeButtonDownPatchConfig(),
      label: getUpgradeBtnTextConfig('#a7a7a7')
    };
  }

  return config;
}
