/* eslint-disable class-methods-use-this */
import { getRibbonPatchConfig } from '../../configs/ninepatch-configs';
import { getLevelTextConfig, getRibbonTextConfig } from '../../configs/text-configs';
import { fitText, makeNinePatch, makeText } from '../../utils';
import { Container } from '../../utils/container';

export class RibbonComponent extends Container {
  constructor() {
    super();
    this._build();
  }

  updateLevelText(level) {
    this._levelsLabel.text = {
      text: `Level Text`,
      macros: { '{{0}}': level },
      ignoreLocalization: false,
      toString: function toString() {
        return this;
      }
    };
  }

  showLevelsText() {
    const hideCreateLabelTween = this.game.add
      .tween(this._createLabel)
      .to({ alpha: 0 }, 300, Phaser.Easing.Sinusoidal.InOut, true);
    hideCreateLabelTween.onComplete.add(() => {
      this._createLabel.visible = false;
    });

    const showLevelsLabel = this.game.add
      .tween(this._levelsLabel)
      .to({ alpha: 1 }, 500, Phaser.Easing.Sinusoidal.InOut, true, 200);
    showLevelsLabel.onStart.add(() => {
      this._levelsLabel.visible = true;
    });

    // @ts-ignore
    hideCreateLabelTween.universal = true;
    // @ts-ignore
    showLevelsLabel.universal = true;
  }

  _build() {
    this._buildBg();
    this._buildLabel();
    this._buildLevelsLabel();
  }

  _buildBg() {
    const bg = makeNinePatch(getRibbonPatchConfig());
    this.addChild((this._bg = bg));
  }

  _buildLabel() {
    const label = makeText(getRibbonTextConfig());
    label.anchor.set(0.5);
    fitText(label, this._bg.width * 0.65);
    this.addChild((this._createLabel = label));
  }

  _buildLevelsLabel() {
    const label = makeText(getLevelTextConfig());
    label.y = 4;
    label.anchor.set(0.5);
    label.visible = false;
    label.alpha = 0;
    fitText(label, this._bg.width * 0.65);
    this.addChild((this._levelsLabel = label));
  }
}
