import { lego } from '@armathai/lego';
import { CharacterState, ClassType, FeaturesType, GenderType, RaceType, WeaponType } from '../../constants';
import { store } from '../../models/store';
import { setCharacterPropertyCommand } from './set-character-property-command';
import { setCharacterStateCommand } from './set-character-state-command';

export function setCharacterFinalSkinCommand() {
  const { gender, weapon, race } = store.game.characterModel;

  const g = gender || GenderType.Male;
  const w = weapon || WeaponType.NormalSword;
  const r = race || RaceType.Human;

  lego.command
    .payload(FeaturesType.Gender, g)
    .execute(setCharacterPropertyCommand)

    .payload(FeaturesType.Race, r)
    .execute(setCharacterPropertyCommand)

    .payload(FeaturesType.Weapon, w)
    .execute(setCharacterPropertyCommand)

    .payload(FeaturesType.Class, ClassType.Angel)
    .execute(setCharacterPropertyCommand)

    .payload(CharacterState.fun)
    .execute(setCharacterStateCommand);
}
