import { getBoardConfig } from '../../configs/board-config';
import { GameState, GenderType } from '../../constants';
import { ObservableModel } from '../observable-model';
import { BoardModel } from './board/board-model';
import { SkillsModel } from './board/skills-model';
import { CharacterModel } from './character-model';

export class GameModel extends ObservableModel {
  constructor() {
    super('GameModel');

    this._boardModel = null;
    this._characterModel = null;
    this._skillsModel = null;

    this._state = GameState.Unknown;

    this.makeObservable('_state', '_characterModel');
  }

  get boardModel() {
    return this._boardModel;
  }

  get skillsModel() {
    return this._skillsModel;
  }

  get characterModel() {
    return this._characterModel;
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  initializeCharacterModel() {
    this._characterModel = new CharacterModel();
  }

  initializeBoardModel() {
    this._boardModel = new BoardModel(getBoardConfig(GenderType[CI_API.Globals.PARAMS.gender_autoselect]));
    this._boardModel.initialize();

    this._state = GameState.CreateCharacter;

    this._boardModel.setCurrentFeatureIndex(0);
  }

  initializeSkillsModel(filled) {
    if (this._skillsModel) {
      return;
    }

    this._skillsModel = new SkillsModel();
    this._state = GameState.UpgradeCharacter;
    this._skillsModel.initialize(filled);
  }

  destroy() {
    this._boardModel && this._boardModel.destroy();
    this._boardModel = null;
  }
}
