/* eslint-disable class-methods-use-this */

import { FiveContentComponent } from './five-content-component';
import { TwoContentComponent } from './two-content-component';

export class FeatureFactory {
  getFeatureView(options, name) {
    let view;
    switch (options.length) {
      case 2:
        view = new TwoContentComponent(name, options);
        break;
      case 5:
        view = new FiveContentComponent(name, options);
        break;

      default:
        break;
    }

    return view;
  }
}
