import { lego } from '@armathai/lego';
import { GameState } from '../../constants';
import { upgradesCompleteGuard } from '../../guards/skill_board/upgrades-complete-guard';
import { itemsToCtaCompleteCommand } from '../ad/cta/items-to-cta-complete-command';
import { setCharacterFinalSkinCommand } from '../character/set-character-final-skin-command';
import { setGameModelStateCommand } from '../game/set-game-model-state-command';

export function onUpgradesCountUpdateCommand() {
  lego.command
    //
    .guard(upgradesCompleteGuard)
    .execute(setCharacterFinalSkinCommand)

    .payload(GameState.UpgradeComplete)
    .guard(upgradesCompleteGuard)
    .execute(setGameModelStateCommand)

    .guard(upgradesCompleteGuard)
    .execute(itemsToCtaCompleteCommand);
}
