/* eslint-disable class-methods-use-this */
import { getTabIconImageConfig, getTabSeparatorImageConfig } from '../../../configs/image-configs';
import { getTabBgPatchConfig } from '../../../configs/ninepatch-configs';
import { getTabTextConfig } from '../../../configs/text-configs';
import { fitText, makeImage, makeNinePatch, makeText } from '../../../utils';
import { Container } from '../../../utils/container';

export class TabComponent extends Container {
  constructor(name, title, iconConfig, bgConfig, separatorConfig) {
    super();

    this._tabName = name;
    this._build(title, iconConfig, bgConfig, separatorConfig);
    this.setPassive();
  }

  get tabName() {
    return this._tabName;
  }

  setActive() {
    this._bg.tint = 0x7cdfff;
    if (this._separator) {
      this._separator.tint = 0xffb52b;
    }
  }

  setPassive() {
    this._bg.tint = 0xc7c7c7;
    if (this._separator) {
      this._separator.tint = 0x7b7b7b;
    }
  }

  setSelected() {
    this._bg.tint = 0xa1f450;
    if (this._separator) {
      this._separator.tint = 0xffb52b;
    }
  }

  _build(title, iconConfig, bgConfig, separatorConfig) {
    this._buildBg(bgConfig);
    this._buildIcon(iconConfig);
    this._buildSeparator(separatorConfig);
    this._buildLabel(title, bgConfig.fitWidth);
  }

  _buildBg(bgConfig) {
    const { frame, width } = bgConfig;
    const img = makeNinePatch(getTabBgPatchConfig(frame, width));
    this.addChild((this._bg = img));
  }

  _buildIcon(iconConfig) {
    const { position, frame } = iconConfig;
    const { x, y } = position;

    const img = makeImage(getTabIconImageConfig(frame));
    img.scale.set(0.95);
    img.position.set(x, y);
    this.addChild((this._icon = img));
  }

  _buildSeparator(separatorConfig) {
    if (separatorConfig) {
      const { x, y } = separatorConfig.position;
      const img = makeImage(getTabSeparatorImageConfig());
      img.position.set(x, y);
      this.addChild((this._separator = img));
    }
  }

  _buildLabel(title, fitWidth) {
    const { x, width } = this._icon;
    const label = makeText(getTabTextConfig(title));
    label.anchor.set(0, 0.5);
    label.position.set(x + width / 2 + 5, 0);
    fitText(label, fitWidth, this._bg.height);

    this.addChild(label);
  }
}
