/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getCtaPlayButtonConfig, getUpgradeButtonConfig } from '../../../configs/button-configs';
import { GameState } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { getViewByProperty } from '../../../utils';
import { FancyButton } from '../../../utils/button/button';
import { Container } from '../../../utils/container';
import { SkillProgressBarComponent } from './skill-progress-bar-component';

function getSkillsConfig() {
  return [
    { name: 'health', title: 'Health', fill: 'red' },
    { name: 'power', title: 'Power', fill: 'yellow' },
    { name: 'armor', title: 'Armor', fill: 'green' }
  ];
}

export class SkillBoardView extends Container {
  constructor() {
    super();

    this.alpha = 0;
    this._build();

    lego.event.on(ModelEvents.SkillsModel.HealthUpdate, this._healthUpdate, this);
    lego.event.on(ModelEvents.SkillsModel.PowerUpdate, this._powerUpdate, this);
    lego.event.on(ModelEvents.SkillsModel.ArmorUpdate, this._armorUpdate, this);
    lego.event.on(ModelEvents.GameModel.StateUpdate, this._onGameStateUpdate, this);
    lego.event.on(ModelEvents.CtaModel.VisibleUpdate, this._onCtaVisibleUpdate, this);
  }

  get name() {
    return 'SkillBoardView';
  }

  get button() {
    return this._button;
  }

  updateBoardPositionInfo() {
    const { x, y } = this;
    this.boardPosition = { x, y };
    this.game.tweens.remove(this.showTween);
    this.position.set(x, y);
  }

  show() {
    if (!this.alpha) {
      this.alpha = 1;
      this.showTween = this.game.add
        .tween(this)
        .from({ y: this.boardPosition.y + LP(-600, 600) }, 500, Phaser.Easing.Sinusoidal.Out, true, 400);
    }
  }

  getProgressBarByName(name) {
    return this.progressBars.find(progress => progress.name === name);
  }

  _healthUpdate(value, prevValue) {
    const progressBar = this.getProgressBarByName('health');
    progressBar.updateProgress(value, prevValue);
  }

  _powerUpdate(value, prevValue) {
    const progressBar = this.getProgressBarByName('power');
    progressBar.updateProgress(value, prevValue);
  }

  _armorUpdate(value, prevValue) {
    const progressBar = this.getProgressBarByName('armor');
    progressBar.updateProgress(value, prevValue);
  }

  _onGameStateUpdate(state, prevState) {
    switch (state) {
      case GameState.UpgradeComplete:
        this._hideButton();
        break;

      default:
        break;
    }
  }

  _onCtaVisibleUpdate(value) {
    if (value) {
      this._hideButton();
      this._buildCtaButton();
    }
  }

  _hideButton() {
    this.game.add
      .tween(this._button)
      .to({ alpha: 0 }, 200, Phaser.Easing.Cubic.InOut, true)
      .onStart.add(() => {
        this._button.switchEnable(false);
      });
  }

  _build() {
    this._buildProgressBars();
    this._buildUpgradeButton();
  }

  _buildProgressBars() {
    const skillsConfig = getSkillsConfig();
    const gap = 25;

    this.progressBars = skillsConfig.map((skillConfig, i) => {
      const { name, title, fill } = skillConfig;
      const progress = new SkillProgressBarComponent(name, title, fill);
      progress.y = i * (progress.height + gap);
      this.addChild(progress);

      return progress;
    });
  }

  _buildUpgradeButton() {
    this.updateTransform();
    const { width, height } = this;
    const gap = 10;
    const btn = new FancyButton(getUpgradeButtonConfig());
    btn.position.set(width / 2, height + btn.height / 2 + gap);
    btn.onClick.add(this._onButtonClick, this);
    this.addChild((this._button = btn));
  }

  _buildCtaButton() {
    const { x, y } = this._button;
    const btn = getViewByProperty('name', 'CTAView').buildPlayButton(getCtaPlayButtonConfig());
    btn.position.set(x, y);
    this.game.add.tween(btn).from({ alpha: 0 }, 300, Phaser.Easing.Cubic.InOut, true);
    this.addChild(btn);
  }

  _onButtonClick() {
    lego.event.emit(ViewEvents.SkillBoardView.UpgradeClick);
  }
}
